<template>
  <div class="user_container">
    <!-- 卡片面板 -->
    <el-card class="box-card">
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column
          label="#"
          width="60"
          type="index"
          sortable
        ></el-table-column>

        <el-table-column label="权限名称" prop="authName"> </el-table-column>
        <el-table-column label="路径" prop="path"> </el-table-column>
        <el-table-column label="权限等级" prop="level">
          <template slot-scope="scope">
            <div>
              <el-button
                type="primary"
                size="mini"
                plain
                v-if="scope.row.level == 0"
                >一级</el-button
              >
              <el-button
                type="success"
                size="mini"
                plain
                v-else-if="scope.row.level == 1"
                >二级</el-button
              >
              <el-button
                type="warning"
                size="mini"
                plain
                v-else-if="scope.row.level == 2"
                >三级</el-button
              >
            </div>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 卡片面板 -->
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      tableData: []
    }
  },
  created () {
    this.getrights()
  },
  methods: {
    async getrights () {
      const res = await this.$http.get('rights/list')
      this.tableData = res.data.data
    }
  }
}
</script>
<style lang="less" scoped>
.user_container {
  height: 100%;
  .el-input {
    width: 40%;
  }
  .el-form {
    .el-input {
      width: 100%;
    }
  }

  .add {
    margin-left: 20px;
  }
  .box-card {
    margin-top: 20px;
  }
}
</style>
