<template>
  <div class="right_container">
    <!-- 卡片面板 -->
    <el-card class="box-card">
      <el-button type="primary" @click="addroles">添加角色</el-button>
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column label="" width="60" type="expand">
          <template slot-scope="scope">
            <div class="rightbox">
              <!-- {{ scope.row }} -->
              <!-- 一级列表 -->
              <el-row v-for="item in scope.row.children" :key="item.id">
                <el-col :span="6" class="el_col2"
                  ><div class="grid-content bg-purple el_col1">
                    <el-tag closable effect="dark"> {{ item.authName }} </el-tag
                    ><i class="el-icon-caret-right"></i></div
                ></el-col>

                <el-col :span="18"
                  ><div class="grid-content bg-purple-light">
                    <!-- 二级列表 -->
                    <el-row v-for="item1 in item.children" :key="item1.id">
                      <el-col :span="6"
                        ><div class="grid-content bg-purple el_col2">
                          <el-tag closable effect="dark" type="success">
                            {{ item1.authName }} </el-tag
                          ><i class="el-icon-caret-right"></i></div
                      ></el-col>
                      <el-col :span="18">
                        <div class="grid-content bg-purple-light">
                          <!-- 三级列表 -->
                          <el-tag
                            v-for="(item2, index2) in item1.children"
                            :key="item2.id"
                            closable
                            effect="dark"
                            type="warning"
                            @close="close(index2)"
                          >
                            {{ item2.authName }}
                          </el-tag>
                        </div></el-col
                      >
                    </el-row>
                  </div></el-col
                >
              </el-row>
            </div>
          </template>
        </el-table-column>

        <el-table-column
          label="#"
          width="60"
          type="index"
          sortable
        ></el-table-column>

        <el-table-column label="角色名称" prop="roleName"> </el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"> </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"
              >编辑</el-button
            >
            <el-button type="danger" icon="el-icon-delete" size="mini"
              >删除</el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="setrights(scope.row)"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 权限弹出框 -->
      <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
        <!-- 树形图 -->
        <el-tree
          :data="data"
          show-checkbox
          node-key="id"
          ref="tree"
          :default-expanded-keys="[2, 3]"
          :default-checked-keys="default_checked_keys"
          :default-expand-all="true"
          :props="defaultProps"
        >
        </el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </el-card>
    <!-- 卡片面板 -->
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      dialogVisible: false,
      tableData: [],
      data: [],
      defaultProps: {
        children: "children",
        label: "authName"
      },
      Uid: 0,
      default_checked_keys: [],
      roleid: 0
    };
  },
  created() {
    this.getrolesdata();
    this.getrightsdata();
  },
  methods: {
    // 获取角色列表
    async getrolesdata() {
      const res = await this.$http.get("roles");
      this.tableData = res.data.data;
    },
    // 获取树形列表
    async getrightsdata() {
      const res = await this.$http.get("/rights/tree");
      this.data = res.data.data;
    },
    setrights(row) {
      console.log(row, 11111111);
      this.dialogVisible = true;
      this.roleid = row.id;
      this.digui(row, this.default_checked_keys);
    },

    // 递归
    digui(row, keysId) {
      if (!row.children) {
        keysId.push(row.id);
        return;
      }
      row.children.forEach(item => {
        this.digui(item, keysId);
      });
    },

    // 点击确认分配权限
    async queren() {
      // console.log(this.$refs.tree.getHalfCheckedNodes())
      // console.log(this.$refs.tree.getCheckedKeys())
      const rightarr = [
        ...this.$refs.tree.getCheckedKeys(),
        ...this.$refs.tree.getHalfCheckedNodes()
      ].join(",");
      await this.$http.post(`roles/${this.roleid}/rights`, {
        rids: rightarr
      });
      this.getrolesdata();
      this.dialogVisible = false;
    },
    // 添加角色
    addroles() {},
    // 删除
    close(index) {
      // console.log(index,666666);
    }
  }
};
</script>
<style lang="less" scoped>
.right_container {
  height: 100%;
  .el-table {
    margin-top: 15px;
  }
  .el-input {
    width: 40%;
  }
  .el-form {
    .sel-input {
      width: 100%;
    }
  }
  .el_col2 {
    border-right: 0;
  }

  .rightbox {
    border-left: 1px solid #efefef;
    border-bottom: 1px solid #efefef;
    .el_col1 {
      display: flex;
      // text-align: center;
      justify-content: center;
      align-items: center;
    }
    .el-col {
      border: 1px solid #efefef;
      border-bottom: 0;
    }
    .el-tag {
      margin: 10px;
    }
  }
}
</style>
